<template>
  <div v-loading="loading" element-loading-text="拼命加载中">
    <div class="handle-box">
      <div class="btn">
        <el-button type="primary" @click="addBuy">添加购买服务</el-button>
      </div>
      <div class="btn">
        <span>学校：</span>
        <el-select v-model="form.school" placeholder="请选择学校">
          <el-option v-for="item in schoolList" :key="item.id" :label="item.name" :value="item.id"></el-option>
        </el-select>
      </div>
    </div>

    <el-dialog title="添加购买服务" :visible.sync="dialogBuy">
      <div class="box">
        <el-form :model="form" label-width="100px">
          <el-form-item label="选择学校">
            <el-select v-model="form.school" placeholder="请选择学校">
              <el-option
                v-for="item in schoolList"
                :key="item.id"
                :label="item.name"
                :value="item.id"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="选择服务">
            <el-checkbox v-model="checkAll" @change="handleCheckAllService">全选</el-checkbox>
            <div class="service">
              <div v-for="(item,index) in serviceList" :key="index">
                <el-checkbox-group v-model="form.service" class="service-item">
                  <el-checkbox :label="item" @change="oneChange">{{item.name}}</el-checkbox>
                </el-checkbox-group>
              </div>
            </div>
          </el-form-item>
          <el-form-item label="商品名称">
            <el-input v-model="form.title"></el-input>
          </el-form-item>
          <el-form-item label="商品价格">
            <el-input v-model="form.price"></el-input>
          </el-form-item>
          <el-form-item label="商品时长">
            <el-input v-model="form.time"></el-input>
          </el-form-item>
          <el-form-item label="商品描述">
            <el-input v-model="form.detail"></el-input>
          </el-form-item>
          <div class="submit">
            <el-form-item>
              <el-button type="primary" @click="newBuy" v-if="isAdd">提交</el-button>
            </el-form-item>
          </div>
        </el-form>
      </div>
    </el-dialog>
    <el-dialog title="订单列表" :visible.sync="showServiceOrder" width="80%">
      <div class="box">
        <div>
          <el-table
            :data="orderData"
            border
            :header-cell-style="{background:'#f0f0f0'}"
            max-height="620"
          >
            <el-table-column prop="no" label="订单ID"></el-table-column>
            <el-table-column prop="user_id" label="用户ID"></el-table-column>
            <el-table-column prop="user_name" label="用户名"></el-table-column>
            <el-table-column prop="name" label="商品名称"></el-table-column>
            <el-table-column prop="price" label="商品价格"></el-table-column>
            <!-- <el-table-column prop="time" label="商品时长"></el-table-column> -->
            <el-table-column prop="status" label="订单状态">
              <template slot-scope="scope">
                <span v-if="scope.row.status == 1">支付中</span>
                <span v-else-if="scope.row.status == 2">已付款</span>
                <span v-else-if="scope.row.status == 3">无效</span>
              </template>
            </el-table-column>
            <el-table-column prop="created_at" label="创建时间"></el-table-column>
          </el-table>
          <div class="block">
            <el-pagination
              @current-change="handleOrderChange"
              :current-page.sync="currentOrderPage"
              :page-sizes="[10, 20, 30, 40, 50]"
              :page-size="orderPageSize"
              layout="sizes, prev, pager, next, jumper"
              :total="orderTotalPage"
              @size-change="handleOrderSizeChange"
            ></el-pagination>
          </div>
        </div>
      </div>
    </el-dialog>

    <el-table :data="tableDate" border :header-cell-style="{background:'#f0f0f0'}" max-height="620">
      <!-- <el-table-column label="名称" type="selection"></el-table-column> -->
      <el-table-column prop="id" label="商品ID" width="100px"></el-table-column>
      <el-table-column prop="title" label="商品名称"></el-table-column>
      <el-table-column prop="price" label="商品价格"></el-table-column>
      <el-table-column prop="time" label="商品时长(天)"></el-table-column>
      <el-table-column prop="detail" label="商品描述"></el-table-column>
      <el-table-column prop="service[0].name" label="服务权限"></el-table-column>
      <el-table-column prop="created_at" label="创建时间"></el-table-column>
      <el-table-column prop="updated_at" label="更新时间"></el-table-column>
      <el-table-column label="操作" width="300px">
        <template slot-scope="scope">
          <el-button size="mini" type="primary" @click="handleEdit(scope.$index,scope.row)">查看服务</el-button>
          <el-button size="mini" type="primary" @click="handleOrder(scope.$index,scope.row)">服务订单</el-button>
          <el-button size="mini" type="danger" @click="handleDel(scope.$index,scope.row)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <!-- 删除提示框 -->
    <el-dialog
      :visible.sync="dialogDel"
      title="删除住户"
      width="20%"
      align="center"
      :close-on-click-modal="false"
    >
      <div style="font-size: 20px; margin-bottom: 30px;">是否删除该服务</div>
      <span>
        <el-button type="primary" @click="toDel">删除</el-button>
        <el-button type="danger" @click="dialogDel = false">取消</el-button>
      </span>
    </el-dialog>

    <div class="block">
      <el-pagination
        @current-change="currentChange"
        :current-page.sync="current"
        :page-sizes="[10, 20, 30, 40, 50]"
        :page-size="size"
        layout="sizes, prev, pager, next, jumper"
        :total="total"
        @size-change="sizeChange"
      ></el-pagination>
    </div>
  </div>
</template>

<script>
import API from "@/api//index.js";

export default {
  name: "buy",
  data() {
    return {
      loading: true,
      dialogDel: false,
      schoolList: [],
      school: "",
      dialogBuy: false,
      showServiceOrder: false,
      checkAll: false, // 全选
      serviceIdList: [],
      serviceList: [
        {
          title: "sendInfo",
          name: "消息推送",
        },
        {
          title: "classesNotice",
          name: "班级公告",
        },
        {
          title: "studentLocal",
          name: "学生定位",
        },
        {
          title: "out-inRecord",
          name: "学生进出记录",
        },
        {
          title: "markInquire",
          name: "成绩查询",
        },
        {
          title: "buyLocator",
          name: "购买定位器",
        },
      ],
      form: {
        title: "",
        price: "",
        detail: "",
        service: [],
        school: "",
        time: "",
      },
      financial: "订单列表",
      financialList: [
        {
          value: 1,
          label: "订单列表",
        },
        {
          value: 2,
          label: "财务统计",
        },
      ],
      orderData: [],
      orderPageSize: 10, // 订单列表
      orderTotalPage: 0,
      currentOrderPage: 1,
      tableDate: [],
      current: 1,
      total: 0,
      size: 10,
      isAdd: true, // 添加
      id: "",
    };
  },
  mounted() {
    this.getBuys();
    this.getSchool();
  },
  methods: {
    getBuys() {
      var self = this;
      API.buys(self.current)
        .then((res) => {
          self.loading = false;
          self.tableDate = res.data;
          self.total = res.total;
        })
        .catch((err) => {
          self.loading = false;
        });
    },
    // 分页
    currentChange(val) {
      var self = this;
      self.current = val;
      self.loading = true;
      API.buys(val, self.size)
        .then((res) => {
          self.loading = false;
          self.tableDate = res.data;
          self.total = res.total;
        })
        .catch((err) => {
          self.loading = false;
        });
    },
    // 每页多少条
    sizeChange(val) {
      var self = this;
      self.size = val;
      self.loading = true;
      API.buys(self.current, val)
        .then((res) => {
          self.loading = false;
          self.tableDate = res.data;
          self.total = res.total;
        })
        .catch((err) => {
          self.loading = false;
        });
    },
    newBuy() {
      var self = this;
      API.buy(self.form).then((res) => {
        self.dialogBuy = false;
        self.$message.success("提交成功");
        self.getBuys();
        self.current = 1;
        self.form = {};
        self.form.service = [];
      });
    },
    addBuy() {
      var self = this;
      self.dialogBuy = true;
      self.isAdd = true;
      self.checkAll = false;

      self.form = {
        title: "",
        price: "",
        detail: "",
        service: [],
        school: "",
        time: "",
      };
    },
    // 获取学校列表
    getSchool() {
      var self = this;
      API.schools(self.current, 100, 2).then((res) => {
        self.schoolList = res.data;
      });
    },
    // 全选服务
    handleCheckAllService(val) {
      var self = this;
      console.log("handleCheckAllService", val);

      self.form.service = val ? self.serviceList : [];
    },
    oneChange() {
      var self = this;
      console.log(self.form.service);
      console.log(self.serviceList);
      self.form.service.length === 6
        ? (self.checkAll = true)
        : (self.checkAll = false);
    },
    // 操作
    handleEdit(index, row) {
      var self = this;
      self.dialogBuy = true;
      self.isAdd = false;
      self.form = row;
      var row_service = [];
      row.service.forEach((item) => {
        row_service.push({
          title: item.title,
          name: item.name,
        });
      });
      self.form.service = row_service;
      console.log(row);
      row.service.length === 6
        ? (self.checkAll = true)
        : (self.checkAll = false);
    },
    handleOrder(index, row) {
      var self = this;
      self.showServiceOrder = true;
      self.product_id = row.id;
      API.server(1, self.size, self.product_id).then((res) => {
        self.orderData = res.data;
        self.orderTotalPage = res.total;
        self.$message.success("获取数据成功");
      });
    },

    // 订单列表分页
    handleOrderChange(val) {
      var self = this;
      self.currentOrderPage = val;
      API.server(val, self.orderPageSize, self.id).then((res) => {
        self.orderData = res.data;
        self.orderTotalPage = res.total;
      });
    },
    // 当前分页
    handleOrderSizeChange(val) {
      var self = this;
      API.server(val, self.currentOrderPage, self.id).then((res) => {
        self.orderData = res.data;
        self.orderTotalPage = res.total;
      });
    },
    handleDel(index, row) {
      var self = this;
      self.dialogDel = true;
      self.id = row.id;
      console.log(row);
    },
    toDel() {
      var self = this;
      API.delBuy(self.id).then((res) => {
        self.$message.success("删除成功");
        self.dialogDel = false;
        self.getBuys();
      });
    },
  },
};
</script>

<style scoped>
.service {
  display: flex;
  flex-wrap: wrap;
}

.service-item {
  margin: 10px;
  padding: 0 10px;
}
</style>
